  methods: {
    removeRow(row) {
      if (this.$refs.xTable._proxy("getTableData").fullData.length > 1) {
        this.$refs.xTable._proxy("remove", row);
      }
    },
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return "页小计";
          }
          if (["real_num", "total_price"].includes(column.property)) {
            var num = this.$utils.sum(data, column.property);
            if (column.property === "real_num") {
              return this.$publicFution.priceFormatNone(num, 0);
            } else {
              this.params.goodsAllprice = num;
              this.computedTotalPrice();
              return this.$publicFution.priceFormatNone(num);
            }
          }
          return null;
        })
      ];
    },
    deleteGoods({ row, $rowIndex }) {
      // if (row.real_num > 1) {
      //   row.real_num--
      //   this.computedRowPrice({ row: row })
      // } else {
      this.tableData.splice($rowIndex, 1);
      this.$nextTick(() => {
        this.$refs.xTable._updateFooter();
      });
      // }
    },
    // 复制商品行
    copyRow(row) {
      this.$refs.xTable._copyRow(row).then(val => {
        val.row.real_num = "";
        val.row.total_price = "";
      });
    },
    // 添加商品
    addGoods({ row }) {
      var item = this.tableData.filter(val => val.batch_id === row.batch_id);
      if (item.length) {
        item[0].real_num++;
        item.unit_price = row.unit_price ? row.unit_price : item.unit_price;
        this.computedRowPrice({ row: item[0] });
      } else {
        var copyRow = this.$publicFution.deepCopy(
          Object.assign(rowParams, row)
        );
        delete copyRow.XID;
        copyRow.real_num = 1;
        copyRow.unit_price = copyRow.guide_price;
        this.computedRowPrice({ row: copyRow });
        this.tableData.push(copyRow);
        this.$refs.xTable._proxy("loadData", this.tableData);
      }
    },
    // 是否可以修改
    changeRowPrice(val) {
      this.currentScope.row.unit_price = val.unit_price;
      this.computedRowPrice(this.currentScope);
    },
    // 查看历史价格
    openHistoryPrice(scope) {
      this.currentScope = scope;
      this.isShowHistoryPrices = true;
    },
    // 获取价格
    getNumber(price, num) {
      if (isNaN(price) || price === "") {
        return num ? num : 0;
      }
      return Number(price);
    },
    // 计算商品行价格
    computedRowPrice({ row }) {
      row.total_price =
        this.getNumber(row.unit_price) * this.getNumber(row.real_num);
      this.$refs.xTable._updateFooter();
    },
    // 计算总价
    computedTotalPrice() {
      var result = this.params.goodsAllprice;
      if (this.params.discount_type == 1) {
        // 打折
        result =
          (this.getNumber(result) *
            this.getNumber(this.params.discount_num, 10)) /
          10;
      } else if (this.params.discount_type == 2) {
        result =
          this.getNumber(result) -
          this.getNumber(this.params.discount_total_amount);
      }
      result += this.getNumber(this.params.service_charge);
      result += this.getNumber(this.params.packing_fee);
      this.params.settlement_price = result.toFixed(2); // 总价
      this.params.arrears = (
        result - this.getNumber(this.params.pay_price)
      ).toFixed(2); // 欠款
    }
  }
