
<template>
  <div class="params" id='dropImg'>
    <div id="leixin">
      <div class="search_container">
        <!-- 销售开单 -->
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="ruleForm" :rules="rules">
          <el-form-item label="销售组织" size="mini">
            {{ params.company_name }}
          </el-form-item>
          <el-form-item label="客户名称" size="mini">
            {{ params.customer_name }}
          </el-form-item>
          <el-form-item label="单据状态：" size="mini">
            {{ params.auditor_status }}
          </el-form-item>
          <el-form-item label="单据类型：" size="mini">
            {{ params.add_status }}
          </el-form-item>
          <el-form-item label="单据号：" size="mini">
            {{ params.outstock_sn }}
          </el-form-item>
          <el-form-item label="单据时间：" size="mini">
            {{ params.add_time }}
          </el-form-item>
        </el-form>
      </div>
      <div class='goodsList'>
        <div class="goodsTitle">
          销售清单列表
        </div>
      </div>
      <div>
        <xTable :maxHeight='200' ref='xTable' rowId='goodsLists' :tableColumn='tableColumn' :isEdit="() => false" :xData='params.goods_list' :footerMethod='footerMethod' />
      </div>
      <div class='costDetail'>
        <el-row :gutter='5'>
          <el-col :span='2'>
            <span class='costDetailTitle'>
              折扣类型：
            </span>
          </el-col>
          <el-col :span='10'>
            <el-radio disabled v-model="params.discount_type" :label="0" @change="computedTotalPrice">无</el-radio>
            <el-radio disabled v-model="params.discount_type" :label="1" @change="computedTotalPrice">整单打折</el-radio>
            <el-input readonly v-model="params.discount_num" class='discountInput' @input="computedTotalPrice" @change="$publicFution.toFixedTwo(params,'discount_num', {greater: 0,max: 10});computedTotalPrice();">
              <span slot="suffix" class="el-input__icon">折</span>
            </el-input>
            <el-radio disabled v-model="params.discount_type" :label="2" @change="computedTotalPrice">整单减</el-radio>
            <el-input readonly v-model="params.discount_total_amount" class='discountInput' @input="computedTotalPrice" @change="$publicFution.toFixedTwo(params,'discountInput', {min: 0});computedTotalPrice();">
              <span slot="suffix" class="el-input__icon">元</span>
            </el-input>
          </el-col>
          <el-col :span='2'>
            <span class='costDetailTitle'>
              其他费用：
            </span>
          </el-col>
          <el-col :span='10'>
            服务费： <el-input readonly v-model="params.service_charge" class='discountInput' @input="computedTotalPrice" @change="$publicFution.toFixedTwo(params,'service_charge', {min: 0});computedTotalPrice();"></el-input>
            包装费： <el-input readonly v-model="params.packing_fee" class='discountInput' @input="computedTotalPrice" @change="$publicFution.toFixedTwo(params,'packing_fee', {min: 0});computedTotalPrice();"></el-input>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <span class='costDetailTitle'>本次收款：</span>
            <el-input readonly v-model="params.pay_price" class='discountInput' @input="computedTotalPrice"></el-input>
            <span>
              欠款： {{ params.arrears }}元
            </span>
          </el-col>
          <el-col :span='12'>
            备注： <el-input type='text' readonly v-model="params.abstract" style="width:80%;" maxlength="200" show-word-limit></el-input>
          </el-col>
        </el-row>
        <div>
          <span class='costDetailTitle'> 配送方式：</span>
          {{ params.delivery_demand_name }}
        </div>
        <div>
          <span class='totalPrice'>
            总金额 <span style="color:red;">{{ params.settlement_price }}</span>
          </span>
        </div>
      </div>
    </div>
    <div class="dialog-footer">
      <el-button @click="goBack">返回</el-button>
    </div>
    <historyPrices v-if='isShowHistoryPrices' v-model="isShowHistoryPrices" @sumbitData='changeRowPrice' :searchParams='{ company_id: params.company_id,goods_id:currentScope.row && currentScope.row.goods_id }' :company_id='params.company_id' />
  </div>
</template>
<script>
import { tableHeader, paramsModel } from "../salesOrder/config/salesOpenOrder";
export default {
  name: "service_station_sales_outstock_see",
  mixins: [tableHeader],
  data() {
    return {
      tableData: [],
      currentScope: {},
      isShowHistoryPrices: false,
      params: this.$publicFution.deepCopy(paramsModel),
    };
  },
  methods: {
    goBack() {
      this.$router.push("/sales/service_station_sales_outstock_list");
    },
    onReset() {
      this.params = this.$publicFution.deepCopy(paramsModel);
      this.$refs.Classification.clear();
      this.$nextTick(() => {
        this.onSearch();
      });
    },
  },
  created() {
    if (this.$route.query.id) {
      this.$api
        .serviceStationGetDeliveryGetSalesOutInfo({
          outstock_id: this.$route.query.id,
        })
        .then(({ data }) => {
          if (data.code == 200) {
            this.params = data.data;
            this.params.arrears = (
              this.params.settlement_price -
              this.getNumber(this.params.pay_price)
            ).toFixed(2); // 欠款
          }
        });
    }
    this.tableColumn = this.$publicFution.filterTableColumn(
      this.tableColumn.filter((val) => val.title !== "操作")
    );
  },
};
</script>

